<template>
  <div class="create-ticket">
    <div style="position: fixed; top: 50%; left: 50%; z-index: 999;">
      <atom-spinner
        v-if="loading"
        :animation-duration="speed"
        :size="config.size"
        :color="spinnersColor"
      />
    </div>
    <div class="mb-3" v-if="processingError">
      <va-notification color="danger" closeable>       
        {{ respErr }}
      </va-notification>
    </div>
  	<div class="row">
      <div class="flex xs12 md12 lg12">
      	<va-card :title="$t('activities.ticket_form')">
          <form @submit.prevent="onsubmit">
            <va-select
              class="mb-4"
              label="Category"
              v-model="formdata.category"
              textBy="code"              
              :options="categories"
              :error = "!!catError.length"
              :error-messages="catError"
              key-by="id"              
            />

            <va-input
              :label="$t('message')"
              type="textarea"
              v-model="formdata.message"
              :error="!!messageErrors.length"
              :error-messages="messageErrors"
            />

            <div class="row mt-5">            
              <va-button type="submit" >
                {{ $t('forms.controls.submit') }}
              </va-button>                        
            </div>
          </form>
        </va-card>
      </div>
    </div>
    <!-- <va-modal
      v-model="showConfirmModal"
      position="top"
      size = "medium"
      :title=" $t('confirm_trans')"
      cancelClass="none"      
      :okText=" $t('modal.confirm') "     
      :cancelText=" $t('modal.cancel')"      
      noOutsideDismiss
      noEscDismiss
      @ok = "FinalPost"
    >
      <table class="va-table va-table--striped va-table--hoverable" width="100%">
        <tbody>
          <tr>
            <td>Account</td>
            <td style="text-align: right;">{{ customer }}</td>
          </tr>
          
          <tr>
            <td>Payment Phone No</td>
            <td style="text-align: right;">{{ paying_phone_no }}</td>
          </tr>
                      
          <tr>
            <td>Amount</td>
            <td style="text-align: right;"> {{ deposit_amount | Currency }} </td>
          </tr> 

          <tr  v-if = "showusd">
            <td>Charges</td>
            <td style="text-align: right;"> {{ total_charges | Currency }} </td>
          </tr>

          <tr v-if = "showusd">
            <td>Total </td>
            <td style="text-align: right;"> {{ total_deduction | Currency }} </td>
          </tr>

          <tr v-if = "showusd">
            <td colspan="2"><font color="red">NB: An extra of Ksh.{{ extra_charge | Currency }} Will be charged on international transactions</font> </td>           
          </tr>  

          <tr>
            <td>Payment Method</td>
            <td style="text-align: right;"> {{ payment_method_name }} </td>
          </tr> 
                
        </tbody>
      </table>
    </va-modal> -->
  </div>
</template>
<script>
import axios from 'axios'
import * as spinners from 'epic-spinners'
export default {
  components: {
    ...spinners      
  },
  name: 'create-ticket',
  data () {
    return {
      userData: JSON.parse(localStorage.getItem('LoggedUser')),
      token: JSON.parse(localStorage.getItem('Token')), 
      loading: false,
      processingError: false,
      respErr:'',
      loadSelect:false,
      showConfirmModal : false,
      config: {
        size: 80,
        duration: 1500,
      },
      spinnersColor: "#4ab0e1",
      currentDuration: 1500,
      sliderDuration: {
        min: 1000,
        max: 2000,
      },
      formdata:{
        user: '',
        category: '',
        message: '',
      },
      categories:[],
      acc_code : {},
      selected : {},
      postdata : {},
      catError : [],
      messageErrors : [],
    }
  },
  computed: {
    speed () {
      return this.sliderDuration.min + this.sliderDuration.max - this.currentDuration
    },

    formReady () {
      return !this.messageErrors.length && !this.catError.length
    }
  }, 
  methods : {
    GetTicketCategories() {
      const self = this

      let config = {
        headers: {
          Authorization: this.userData.user_id+' '+this.token
        }
      }

      axios.post(`${self.$apiUrl}getTicketCategories`, this.userData.user_id, config)
        .then(function (res) {
          res = res.data 
          self.categories = res.categories
          console.log(res);          
        })
        .catch(function (error) {
          console.log(error)
        })
    },

    onsubmit () {
      this.processingError = false;

      this.catError =  this.formdata.category.id ? [] : ['Please Select a category']
      this.messageErrors = this.formdata.message ? [] : ['Amount is required']
  
      if (this.formReady) {        
        this.loading = true
        var self = this

        let config = {
          headers: {
            Authorization: this.userData.user_id+' '+this.token
          }
        }

        var postdata = {
          user: this.formdata.user,
          category: this.formdata.category.id,
          message: this.formdata.message,
        };

        axios.post(`${this.$apiUrl}CreateTicket`, postdata, config)
        .then(function (res) {
          self.loading = false        
          const resp = res.data



          if(resp.response.success == '1'){              
            self.showToast(`Ticket was succefully Raised`, {
              position: 'bottom-right',
              icon: "fa-check",
              duration: 2400,
            })
            setTimeout( () => self.$router.push({name: "tickets-list", params: {}}), 2500);
          } else {
            self.processingError = true
            self.respErr = resp.response.message
            self.showToast(resp.response.message, {
              position: 'bottom-right',
              icon: "fa-check",
              duration: 2500,
            })
          }           
         
        })
        .catch(function (error) {
            console.log(error)
        })
        
      }
    }

  },
  created () { 
    this.formdata.phone_no  = this.userData.mobile_number
    this.customer  = this.userData.member_number
    this.formdata.user  = this.userData.user_id 
    this.GetTicketCategories();   
  } 
} 
</script>